/* Global CSS to ensure full viewport width and remove restrictions */
html, body, #root, .App {
  width: 100% !important;
  max-width: 100% !important;
  min-width: 0 !important;
  margin: 0 !important;
  padding: 0 !important;
  box-sizing: border-box;
  overflow-x: hidden;
}

/* Ensure all content boxes respect container width */
*, *::before, *::after {
  box-sizing: border-box;
  max-width: 100%;
}

/* Improve image and media rendering */
img, video, iframe {
  max-width: 100%;
  height: auto;
}

/* Ensure overflow text wrapping */
p, h1, h2, h3, h4, h5, h6, span, div {
  overflow-wrap: break-word;
  word-wrap: break-word;
  hyphens: auto;
}

/* Fix for project section overflow issues */
.project-full-content {
  max-width: 100vw;
  overflow-x: hidden;
}
